util.jwxpay = function(options, e, s) {
	console.log(options)
	if (uni.getStorageSync("logintype") == 1) {
		window.location.href = options.mweb_url
	} else {


		jweixin.config({
			appId: options.appId, // 
			timestamp: options.timeStamp, // 
			nonceStr: options.nonceStr, // 
			signature: options.paySign, // 
			jsApiList: ['chooseWXPay', 'openAddress'] // JS
		});
		jweixin.ready(() => {
			jweixin.chooseWXPay({
				timestamp: options.timeStamp,
				nonceStr: options.nonceStr, //  32 
				package: options.package, // prepay_id
				signType: options.signType, // 'SHA1''MD5'
				paySign: options.paySign, // 
				success: function(res) { //
					setTimeout(function() {
						if (s == '0') {
							uni.reLaunch({
								url: e
							})
						} else if (s == '1') {
							uni.navigateBack({
								delta: 1
							});
						} else {
							uni.navigateTo({
								url: e
							})
						}
					}, 1500)
					util.balance()
					uni.showToast({
						title: '',
						duration: 1000,
						icon: "none"
					});
				}
			});
		});
	}
}
