util.isIosX = function() {
	var isIos = false
	uni.getSystemInfo({
		success(res) {
			var modelmes = res.model;
			// 
			if (modelmes.search('iPhone X') != -1 || modelmes.search('iPhone 11') != -
				1) { //IPhoneX68rpx
				isIos = true
			} else {
				isIos = false
			}
		}
	})
	return isIos
}

