import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
	// 
	state: {
		hasLogin: false,
		userInfo: {},
	},
	// 
	// (--->$store.commit(""))
	mutations: {
			sum(state,a){
				console.log("mutations",a)
				console.log("mutations",state)
				return a;
			}
	},
	// 
	// actions--->this.$store.dispatch("")
	actions: {
			sum(state,aObj){
				console.log("actions",aObj)
			}
	}
})

export default store
